<template>
  <div>
    <!-- 搜索栏 -->
    <form action="/">
      <van-search
        v-model="value"
        show-action
        placeholder="请输入影院名称"
        @cancel="onCancel"
      />
    </form>
    <!-- 搜索栏 -->
    <!-- 搜索栏下方列表 -->
    <ul v-if="value">
      <li v-for="data in computedList" :key="data.cinemaId">
        <div class="left">
          <div class="cinema_name">{{ data.name }}</div>
          <div class="cinema_text">{{ data.address }}</div>
        </div>
        <div class="right cinema_price">
          ￥<span style="font-size: 15px">{{ data.lowPrice / 100 }} </span>起
        </div>
      </li>
    </ul>
    <!-- 搜索栏下方列表 -->
  </div>
</template>
<script>
export default {
  data () {
    return {
      value: ''
    }
  },
  methods: {
    // 点击取消跳转回上一级
    onCancel () {
      this.$router.back()
    }
  },
  computed: {
    // 计算过滤出含有输入关键词的名称和地址
    computedList () {
      // filter过滤，toUpperCase转换为大写
      return this.$store.state.cinemaList.filter(item => item.name
        .toUpperCase().includes(this.value.toUpperCase()) ||
        item.address
          .toUpperCase().includes(this.value.toUpperCase()))
    }
  },
  mounted () {
    if (this.$store.state.cinemaList.length === 0) {
      // 数据分发
      this.$store.dispatch('getCinemaData', this.$store.state.cityId)
    } else {
      console.log('缓存')
    }
  }
}
</script>
