import Vue from 'vue'
import Vuex from 'vuex'
import http from '@/util/http'
import createPersistedState from 'vuex-persistedstate'
Vue.use(Vuex)

export default new Vuex.Store({
  // 持久化存储
  plugins: [createPersistedState({
    reducer: (state) => {
      return {
        cityId: state.cityId,
        cityName: state.cityName,
        cinemaName: state.cinemaName
      }
    }
  }
  )],
  // state公共状态
  state: {
    cityId: '310100',
    cityName: '上海',
    cinemaList: [],
    cinemaName: '',
    isTabarShow: true
  },
  getters: {
  },
  // 统一管理,被devtools记录状态的修改
  mutations: {
    changeCityName (state, CityName) {
      state.cityName = CityName
    },
    changeCityId (state, CityId) {
      state.cityId = CityId
    },
    changeCinemaData (state, Cinemas) {
      state.cinemaList = Cinemas
    },
    changeCinemaName (state, CinemaName) {
      state.cinemaName = CinemaName
    },
    clearCinema (state) {
      state.cinemaList = []
    },
    show (state) {
      state.isTabarShow = true
    },
    hide (state) {
      state.isTabarShow = false
    }
  },
  // 异步处理，支持异步和同步
  actions: {
    // 异步请求数据
    getCinemaData (store, cityId) { // 这里可调用所传参数cityId，也可以store.state.cityId
      // return返回一个promise对象，后面可跟.then()
      return http({
        url: `/gateway?cityId=${cityId}&ticketFlag=1&k=1942800`,
        headers: {
          'X-Host': 'mall.film-ticket.cinema.list'
        }
      }).then(res => {
        store.commit('changeCinemaData', res.data.data.cinemas)
      })
    }
  },
  modules: {
  }
})
