<template>
    <div v-if="filmInfo">
      <!-- 标题导航栏 -->
      <detail-header v-scroll="50">
        {{filmInfo.name}}
      </detail-header>
      <!-- 标题导航栏 -->

      <!-- 海报 -->
      <div :style="{
        backgroundImage:'url('+filmInfo.poster+')'
      }" class="poster"></div>
      <!-- 海报 -->

      <!-- 影片信息栏 -->
      <div class="content">
        <div class="item">
          <div class="content-name">{{filmInfo.name}}</div>
          <div>
            <div class="detail-text">{{filmInfo.category}}</div>
            <div class="detail-text">{{filmInfo.premiereAt|dateFilter}}上映</div>
            <div class="detail-text">{{filmInfo.nation}} | {{filmInfo.runtime}}分钟</div>
            <!-- 影片介绍 -->
            <div class="detail-text" style="line-height:19px;padding-top:12px;"
            :class="isHidden?'hidden':'' ">
            {{filmInfo.synopsis}}
            </div>
            <div @click=" isHidden = !isHidden " style="text-align:center;">
              <i class="iconfont" style="font-size:13px;" :class="isHidden?'icon-down':'icon-upward'"></i>
            </div>
          </div>
        </div>
        <!-- 影片信息栏 -->

        <!-- 演职人员 -->
        <div class="item">
          <div style="padding:15px 15px 15px 0;margin-top:10px;">演职人员</div>
          <detail-swiper :PerView="4" name="actors">
            <detail-swiper-item v-for="data in filmInfo.actors" :key="data.role">
              <div :style="{
              backgroundImage:'url('+data.avatarAddress+')'
              }" class="avatar"></div>
              <div style="text-align:center;font-size:12px;color: #191a1b">{{data.name}}</div>
              <div style="text-align:center;font-size:10px;color: #797d82;">{{data.role}}</div>
            </detail-swiper-item>
          </detail-swiper>
        </div>
        <!-- 演职人员 -->

        <!-- 剧照 -->
        <div class="item">
          <div style="padding:15px 15px 15px 0;margin-top:10px;">剧照</div>
          <detail-swiper :PerView="2" name="photo">
            <detail-swiper-item v-for="(data,index) in filmInfo.photos" :key="index">
              <div :style="{
              backgroundImage:'url('+data+')'
              }" class="avatar"
              @click="handlePreview(index)"></div>
            </detail-swiper-item>
          </detail-swiper>
        </div>
        <!-- 剧照 -->
      </div>
      <!-- 购票按钮 -->
      <van-button class="button" type="primary" size="large"
      color="#ff5f16" @click="handleClick">选座购票</van-button>
    </div>
</template>
<script>
import http from '@/util/http'
import Vue from 'vue'
import moment from 'moment'
import detailSwiper from '@/components/detail/DetailSwiper'
import detailSwiperItem from '@/components/detail/DetailSwiperItem'
import detailHeader from '@/components/detail/DetailHeader'
import { ImagePreview } from 'vant'
import obj from '@/util/mixinsObj'

Vue.filter('dateFilter', (data) => {
  return moment(data * 1000).format('YYYY-MM-DD')
})
// 自定义指令scroll
Vue.directive('scroll', {
  inserted (el, binding) {
    // 开始隐藏标题导航栏
    el.style.display = 'none'
    // window.onscroll监听窗口滚动条
    window.onscroll = () => {
      // 当滚动条滚动 > binding.value，显示导航栏
      if ((document.documentElement.scrollTop || document.body.scrollTop) > binding.value) {
        el.style.display = 'block'
      } else { // 否则隐藏
        el.style.display = 'none'
      }
    }
  },
  // 销毁组件时执行
  unbind () {
    // 取消滚动条监听
    window.onscroll = null
  }
})
export default {
  mixins: [obj],
  data () {
    return {
      filmInfo: null,
      isHidden: true
    }
  },
  // 组件注册
  components: {
    detailSwiper,
    detailSwiperItem,
    detailHeader
  },
  methods: {
    // 影片介绍栏点击隐藏/显示
    handleHidden () {
      this.isHidden = !this.isHidden
    },
    // 剧照栏单击图片全屏浏览
    handlePreview (index) {
      ImagePreview({
        images: this.filmInfo.photos, // 图片地址列表
        closeable: true, // 是否显示全屏浏览关闭按钮
        closeIconPosition: 'top-left', // 全屏浏览关闭按钮位置
        startPosition: index // 显示所点击的为第几张剧照
      })
    },
    // 点击购票按钮跳转
    handleClick () {
      this.$router.push('/order')
    }
  },
  created () {
    // 请求详情页数据
    http({
      url: `/gateway?filmId=${this.$route.params.myid}&k=665553`,
      headers: {
        'X-Host': 'mall.film-ticket.film.info'
      }
    }).then(res => {
      this.filmInfo = res.data.data.film
    })
  }
}
</script>
