  .content-name{
    font-size: 1.125rem;
    color:#191a1b;
    height: 1.5rem;
    line-height: 1.5rem;
    padding-top: .75rem;
  }
  .detail-text{
    font-size: .8125rem;
    color: #797d82;
    padding-top: .25rem;
  }
}
.hidden{
  overflow:hidden;
  height: 2.375rem;
}
.button{
  position: fixed;
  bottom:0;
  z-index: 100;
}
</style>
