<template>
    <div>
        <!-- 即将上映列表组件 -->
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="到底啦"
          @load="onLoad"
          :immediate-check="false"
        >
          <!-- 列表每一项 -->
          <van-cell v-for="data in datalist" :key="data.filmId" @click="handleChangePage(data.filmId)">
            <img :src="data.poster" />
            <div class="text">
              <div class="title">{{data.name}}</div>
              <div class="content">
                <div class="actors">
                  主演：{{data.actors | actorsFilter}}
                </div>
                <div class="">
                  上映日期：{{computedDate(data.premiereAt)}}
                </div>
              </div>
            </div>
            <button class="button" v-show="data.isPresale" @click="handleButton(data)">预购</button>
          </van-cell>
          <!-- 列表每一项 -->
        </van-list>
        <!-- 即将上映列表组件 -->
    </div>
</template>
<script>
import http from '@/util/http'
import moment from 'moment'
export default {
  data () {
    return {
      datalist: [],
      total: 0, // 数据长度，初始为0
      current: 1, // 页码数，初始为1
      loading: false, // false表示没有加载，true表示正在加载
      finished: false, // true则表示数据全部加载完毕
      isPresale: true // 购票按钮是否显示
    }
  },
  mounted () {
    // 第一次请求数据
    http({
      url: `/gateway?cityId=${this.$store.state.cityId}&pageNum=1&pageSize=10&type=2&k=9146778`,
      headers: {
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then(res => {
      this.datalist = res.data.data.films
      this.total = res.data.data.total // 记录数据总长度
    })
  },
  methods: {
    // 页面到底时请求下一页数据
    onLoad () {
      this.current++ // 页码数+1
      // 异步更新数据
      http({
        url: `/gateway?cityId=${this.$store.state.cityId}&pageNum=${this.current}&pageSize=10&type=2&k=9146778`,
        headers: {
          'X-Host': 'mall.film-ticket.film.list'
        }
      }).then(res => {
        // 新老数据合并赋给datalist
        this.datalist = [...this.datalist, ...res.data.data.films]
        this.loading = false // 加载状态结束
      })
      // 数组长度等于数据总长度且不为0，数据全部加载完成
      if (this.datalist.length === this.total && this.total !== 0) {
        this.finished = true
      }
    },
    // 给列表每一项添加点击事件，跳转到详情页
    handleChangePage (id) {
      this.$router.push({
        name: 'xuDetail',
        params: {
          myid: id
        }
      })
    },
    // 给购票按钮添加点击事件
    handleButton (data) {
      // 向store中Mutations提交修改changeCinemaName请求，传入cinemaName
      this.$store.commit('changeCinemaName', data.name)
      // 跳转到影院列表页
      this.$router.push({
        name: 'Shop',
        params: {
          // 路由跳转传入myid
          myid: data.filmId,
          cinema: data.name
        }
      })
    },
    // 上映日期事件处理
    computedDate (params) {
      return moment(params * 1000).format('dddd MM月DD日')
    }
  }
}
</script>
