<template>
  <div class="swiper" :class="name">
    <div class="swiper-wrapper">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import Swiper from 'swiper/bundle'
import 'swiper/css/bundle'
export default {
  props: {
    // 轮播图显示张数
    PerView: {
      type: Number,
      default: 1
    },
    // 轮播图名称
    name: {
      type: String,
      default: 'Xu'
    }

  },
  mounted () {
    new Swiper('.' + this.name, {
      slidesPerView: this.PerView,
      spaceBetween: 10, // 图和图之间间隔
      freeMode: true
    })
  }
}
</script>
<style scoped lang="scss">
.Xu{
  margin-left: .9375rem;
}
</style>
