<template>
    <div>
      <!-- 影院导航栏 -->
      <van-nav-bar title="影院" @click-left="handleClickLeft" @click-right="handleClickRight">
        <template #left>
          {{ $store.state.cityName }}<van-icon name="arrow-down" size="12" color="black"/>
        </template>
        <template #right>
          <van-icon name="search" size="24" color="black"/>
        </template>
      </van-nav-bar>
      <!-- 影院导航栏 -->

      <!-- 影院列表 -->
      <div class="box" :style="{
                height:height
            }">
            <ul>
                <li v-for="data in $store.state.cinemaList" :key="data.cinemaId">
                    <div class="left">
                        <div class="cinema_name">{{data.name}}</div>
                        <div class="cinema_text">{{data.address}}</div>
                    </div>
                    <div class="right cinema_price">
                        ￥<span style="font-size:15px;">{{data.lowPrice/100}}
                    </span>起</div>
                </li>
            </ul>
      </div>
      <!-- 影院列表 -->
    </div>
</template>
<script>
import BetterScroll from 'better-scroll'
export default {
  data () {
    return {
      height: '0px'
    }
  },
  mounted () {
    // Tabar组件显示
    this.$store.commit('show')
    // 动态计算高度
    this.height = document.documentElement.clientHeight -
    document.querySelector('.van-nav-bar').offsetHeight -
    document.querySelector('footer').offsetHeight + 'px'

    // 如果cinemaList长度为0，第一次请求cinemaList数据
    // 向state异步发起请求
    if (this.$store.state.cinemaList.length === 0) {
      // 数据分发
      this.$store.dispatch('getCinemaData', this.$store.state.cityId)
        .then(res => {
          // 滚动条
          this.$nextTick(() => {
            new BetterScroll('.box', {
              scrollbar: {
                fade: true
              }
            })
          })
        })
    } else { // cinemaList长度不为0，则有缓存数据
      // 滚动条
      this.$nextTick(() => {
        new BetterScroll('.box', {
          scrollbar: {
            fade: true
          }
        })
      })
    }
  },
  methods: {
    // 点击影院左侧城市按钮进入city城市选择页面
    handleClickLeft () {
      this.$router.push('/city')
      // 点击城市切换时，原城市数据失去意义，清空cniemaList缓存
      this.$store.commit('clearCinema')
    },
    // 点击影院右侧搜索框按钮进入search搜索框页面
    handleClickRight () {
      this.$router.push('/cinemas/search')
      // 切换search时隐藏Tabar组件
      this.$store.commit('hide')
    }
  }
}
</script>
<style scoped lang="scss">
li{
  background-color: #fff;
  padding: .9375rem;
  display: flex;
  justify-content: space-between;
  border-bottom: .0625rem solid #ededed;
  .left{
      width: 17.5rem;
      margin-right: .9375rem;
      .cinema_name{
          color: #191a1b;
          font-size: .9375rem;
      }
      .cinema_text{
          width: 13.25rem;
          color: #797d82;
          font-size: .75rem;
          margin-top: .3125rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
      }
  }
  .right{
      width: 4.375rem;
      color:#ff5f16;
      text-align: center;
  }
  .cinema_price{
      font-size: .6875rem;
  }
}
.box{
    overflow: hidden;   // 溢出隐藏
    position: relative; // 修正滚动条位置
}
</style>
