<template>
  <div>
    <van-index-bar :index-list="computedList" @select="handleSelect">
      <div v-for="data in cityList" :key="data.type">
          <van-index-anchor :index="data.type" />
          <van-cell :title="city.name" v-for="city in data.list" :key="city.cityId"
          @click="handleCity(city)"/>
      </div>
    </van-index-bar>
  </div>
</template>
<script>
import http from '@/util/http'
import { Toast } from 'vant'
import obj from '@/util/mixinsObj'
export default {
  mixins: [obj],
  data () {
    return {
      cityList: []
    }
  },
  computed: {
    // 计算生成右侧字母索引表
    computedList () {
      // map 将citylist重新映射为每一项是item.type（字母）
      return this.cityList.map(item => item.type)
    }
  },
  mounted () {
    // 请求城市列表数据
    http({
      url: '/gateway?k=1184087',
      headers: {
        'X-Host': 'mall.film-ticket.city.list'
      }
    }).then(res => {
      console.log(res.data.data.cities)
      // 所有数据进行分组转换
      this.cityList = this.renderCity(res.data.data.cities)
    })
  },
  methods: {
    // 点击右边字母列表弹窗出所点击的字母
    handleSelect (data) {
      Toast(data)
    },
    // 点击每一项城市，跳转切换
    handleCity (item) {
      console.log(item)
      // 修改城市id和城市名称
      this.$store.commit('changeCityName', item.name)
      this.$store.commit('changeCityId', item.cityId)
      // 返回上一级
      this.$router.back()
    },
    // 城市数据转换
    renderCity (list) {
      // 存储26个字母的数组
      const cityList = []
      // 存储生成新数组
      const letterList = []
      // 循环生成26个字母，写入letterList数组
      for (let i = 65; i < 91; i++) {
        letterList.push(String.fromCharCode(i))
      }
      // 字母数组遍历
      letterList.forEach(letter => {
        // filter过滤出首字母等于letter的数据
        const newList = list.filter(item =>
        // substring截取字符串，toUpperCase首字母转换成大写
          item.pinyin.substring(0, 1).toUpperCase() === letter)
        // newList长度大于则存在含该首字母的城市，写入
        newList.length > 0 && cityList.push({
          type: letter,
          list: newList
        })
      })
      return cityList
    }
  }
}
</script>
<style lang="scss">
.van-toast--html, .van-toast--text{
    min-width: 1.875rem;
}
</style>
