const date = {
  zhDate (item) {
    if (item === 0) {
      return '周日'
    }
    if (item === 1) {
      return '周一'
    }
    if (item === 2) {
      return '周二'
    }
    if (item === 3) {
      return '周三'
    }
    if (item === 4) {
      return '周四'
    }
    if (item === 5) {
      return '周五'
    }
    if (item === 6) {
      return '周六'
    }
  }
}
export default date
