<template>
  <div>
    <!-- 导航标题栏 -->
    <van-nav-bar
      :title="$store.state.cinemaName"
      @click-left="handleClickLeft"
      @click-right="handleClickRight"
    >
      <template #left>
        <i class="iconfont icon-back"></i>
      </template>
      <template #right>
        <van-icon name="search" size="24" color="black" />
      </template>
    </van-nav-bar>
    <!-- 导航标题栏 -->

    <!-- 标签栏 -->
    <van-tabs
      v-model:active="active"
      title-active-color="#ff5f16"
      line-height="2px"
      line-width="79px"
      border
    >
      <van-tab
        v-for="data in datalist"
        :title="handleDate(data.showDate)"
        :key="data.showDate"
      >
        <!-- 影院列表部分 -->
        <ul>
          <li v-for="cinema in cinemaListFilter(data.cinemaList)" :key="cinema.cinemaId">
            <div class="left">
              <div class="cinema_name">{{ cinema.name }}</div>
              <div class="cinema_text">{{ cinema.address }}</div>
            </div>
            <div class="right cinema_price">
              ￥<span style="font-size: 15px">{{ cinema.lowPrice / 100 }} </span
              >起
            </div>
          </li>
        </ul>
        <!-- 影院列表部分 -->
      </van-tab>
      <!-- 标签栏 -->
    </van-tabs>
  </div>
</template>
<script>
import http from '@/util/http'
import { ref } from 'vue'
import moment from 'moment'
import date from '@/util/date'

export default {
  // 标签栏参数
  setup () {
    const active = ref(0)
    return { active }
  },
  data () {
    return {
      datalist: []
    }
  },
  methods: {
    // 点击导航栏左侧返回上一级
    handleClickLeft () {
      this.$router.back()
    },
    // 点击导航栏右侧进入搜索页
    handleClickRight () {
      this.$router.push('/cinemas/search')
    },
    // 标签栏日期名称处理
    handleDate (data) {
      date.zhDate(moment(data * 1000).day())
      return moment(data * 1000).format('M月DD日')
    },
    // 传入cinemaId列表，从store中取出影院列表数据
    // 过滤出含有cinemaId列表的cinema数据
    cinemaListFilter (list) {
      const newlist = []
      list.forEach(res => {
        this.$store.state.cinemaList.forEach(item => {
          if (item.cinemaId === res) {
            newlist.push(item)
          }
        })
      })
      return newlist
    }
  },
  mounted () {
    // Tabar组件隐藏
    this.$store.commit('hide')
    // 发起ajax请求获取cinemaId列表数据
    http({
      url: `/gateway/?filmId=${this.$route.params.myid}&cityId=${this.$store.state.cityId}&k=2450516`,
      headers: {
        'X-Host': 'mall.film-ticket.cinema.film-show-cinema'
      }
    }).then((res) => {
      // 以showDate属性用sort（）升序排序好数组
      this.datalist = res.data.data.showCinemas.sort((a, b) => { return a.showDate - b.showDate })
    })
    // 若第一次请求cinemaList数据，向state异步发起请求
    if (this.$store.state.cinemaList.length === 0) {
      // 数据分发
      this.$store.dispatch('getCinemaData', this.$store.state.cityId)
    } else { // 不是第一次请求该数据，则使用缓存
      console.log('缓存')
    }
  }
}
</script>
